import React, { Component } from 'react';
import { Navbar, Nav, NavItem, NavDropdown, MenuItem } from 'react-bootstrap';
import logo from '../assets/progenitorLogo.png';
import './topNavbar.css';

var logoStyle = {
  cursor: 'pointer'
}

class TopNavbar extends Component {
  render() {
    return (
      <div>
        <Navbar staticTop inverse collapseOnSelect className="header">
          <Navbar.Header>
            <Navbar.Brand>
              <img style={logoStyle} src={logo} alt="Cerebrate" onClick={() => {this.props.router.navigate("", {trigger: true})}} />
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>
          <Navbar.Collapse>
            <Nav>
              <NavItem eventKey={1} onClick={() => {this.props.router.navigate("about", {trigger: true})}}>
                About
              </NavItem>
              <NavItem eventKey={2} onClick={() => {this.props.router.navigate("blog", {trigger: true})}}>
                Blog
              </NavItem>
              <NavItem eventKey={3} onClick={() => {this.props.router.navigate("contact", {trigger: true})}}>
                Contact
              </NavItem>
            </Nav>
            <Nav pullRight>
              <NavDropdown eventKey={1} title="Services" id="basic-nav-dropdown">
                <MenuItem eventKey={1.1} onClick={() => {this.props.router.navigate("services/imaging", {trigger: true})}}>Imaging</MenuItem>
                <MenuItem divider />
                <MenuItem eventKey={1.2} onClick={() => {this.props.router.navigate("services/priceprediction", {trigger: true})}}>Price Prediction</MenuItem>
              </NavDropdown>
              <NavItem eventKey={2} href="#">
                Sign In
              </NavItem>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </div>
    );
  }
}

              // <a onClick={() => {this.props.router.navigate("", {trigger: true})}}>Cerebrate</a>
export default TopNavbar



