import React, { Component } from 'react';
import { Well } from 'react-bootstrap';
import TopNavbar from '../components/topNavbar';
import SignUpForm from '../components/signUpForm';
import Footer from '../components/footer';
import logo from '../assets/progenitorLogo.png';
import bg1 from '../assets/cityscape1_bg.JPG';
import '../App.css';
import './landing.css';

var appHeaderStyle = {
  backgroundImage: "url(" + bg1 + ")",
  backgroundSize: "cover"
};

class Landing extends Component {
  render() {
    return (
      <div className="app-container">
        <TopNavbar router={this.props.router} />
        <div className="App">
          <header className="App-header" style={appHeaderStyle} >
            <img src={logo} className="App-logo" alt="logo" />
            <h1 className="App-title">Accessible, Intuitive, Artificial Intelligence</h1>
            <p className="App-intro">Think Faster, Know More, and Unlock Your True Potential</p>
            <Well className="sign-up-form-container">
              <SignUpForm/>
            </Well>
          </header>
        </div>
        <Footer/>
      </div>
    );
  }
}

export default Landing;



