import React, { Component } from 'react';
import TopNavbar from '../components/topNavbar';
import Footer from '../components/footer';
import aboutLiam from '../assets/about_liam.JPG';
import '../App.css';
import './about.css';

var profilePictureStyle = {
  backgroundImage: "url(" + aboutLiam + ")",
  backgroundSize: "cover",
  backgroundPosition: "center"
};

class About extends Component {
  render() {
    return (
      <div className="app-container">
        <TopNavbar router={this.props.router} />
        <h1 className="headline">About Cerebrate</h1>
        <div className="profile-container">
          <div className="profile-description-container">
            <div className="profile-description">
              <h3 className="title">Liam Yafuso, Founder</h3>
              <hr/>
              <p>
        I've always had a passion for technology, and for me computers always stuck out over every other piece of tech.
        Even as kid I was enraptured with the concept of artificial intelligence, and what it would mean for the future.
        I feel incredibly lucky to get to live at the point in history where I can actually participate in building artificial intelligence technologies.
        I've thoroughly enjoyed every AI system I've worked on, from facial recognition, to price prediction, to medical imaging analysys.
        It's going to be a brilliant future, and I look forward to helping people and organizations integrate this new tech into their everyday lives.
              </p>
            </div>
          </div>
          <div className="profile-picture-container">
            <div className="profile-picture" style={profilePictureStyle} />
          </div>
        </div>
        <div className="statement"></div>
        <Footer router={this.props.router}/>
      </div>
    );
  }
}

export default About;



