import React, { Component } from 'react';
import { FormGroup, FormControl, ControlLabel, HelpBlock, Button } from 'react-bootstrap';
import './signUpForm.css';

var formTitleStyle = {
  textAlign: "center",
  margin: "0% 0% 5% 0%"
}

class SignUpForm extends Component {
  constructor(props, context) {
    super(props, context);

    this.handleChange = this.handleChange.bind(this);

    this.state = {
      value: ''
    };
  }

  getValidationState() {
    const length = this.state.value.length;
    if (length > 10) return 'success';
    else if (length > 5) return 'warning';
    else if (length > 0) return 'error';
    return null;
  }

  handleChange(e) {
    this.setState({ value: e.target.value });
  }

  render() {
    return (
      <form>
        <h2 className="form-title" style={formTitleStyle}>Sign Up</h2>
        <FormGroup controlId="formBasicText">
          <ControlLabel>Username</ControlLabel>
          <FormControl
            type="text"
            placeholder="Pick a username"
          />
          <FormControl.Feedback />
        </FormGroup>

        <FormGroup controlId="formBasicText">
          <ControlLabel>Email</ControlLabel>
          <FormControl
            type="text"
            placeholder="me@myemail.com"
          />
          <FormControl.Feedback />
        </FormGroup>

        <FormGroup
          controlId="formBasicText"
          validationState={this.getValidationState()}
        >
          <ControlLabel>Password</ControlLabel>
          <FormControl
            type="text"
            value={this.state.value}
            placeholder="Create a password"
            onChange={this.handleChange}
          />
          <FormControl.Feedback />
          <HelpBlock>Use at least one letter, one numeral, and seven characters.</HelpBlock>
        </FormGroup>
        <Button type="submit" className="sign-up-button">Sign up for Cerebrate</Button>
      </form>
    );
  }
}

export default SignUpForm;



