import Backbone from 'backbone';
import React from 'react';
import ReactDOM from 'react-dom';
import Landing from './layouts/landing';
import About from './layouts/about';
import Contact from './layouts/contact';

var Router = Backbone.Router.extend({
  routes: {
    "": "index",
    "about": "about",
    "blog": "blog",
    "contact": "contact",
    "services/imaging": "servicesImaging",
    "services/priceprediction": "servicesPricePrediction",
  },

  index: function() {
    ReactDOM.render(<Landing router={this} />, document.getElementById('root'));
  },

  about: function() {
    ReactDOM.render(<About router={this} />, document.getElementById('root'));
  },

  blog: function() {
    ReactDOM.render(<About router={this} />, document.getElementById('root'));
  },

  contact: function() {
    ReactDOM.render(<Contact router={this} />, document.getElementById('root'));
  },

  servicesImaging: function() {
    ReactDOM.render(<About router={this} />, document.getElementById('root'));
  },

  servicesPricePrediction: function() {
    ReactDOM.render(<About router={this} />, document.getElementById('root'));
  },


});

export default Router;



